@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
html,
body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #121212;
  color: #fff;
}

.main {
  padding-left: 5rem;
}

.font-header {
  font-family: "Lato";
}

.font-primary {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.align-screen--vertical-centered {
  display: flex;
  align-items: center;
  height: 100%;
}

.align-screen--centered {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

@media screen and (max-width: 480px) {
  .main {
    padding-left: 2rem;
  }
}

@media screen and (max-width: 400px) {
  .main {
    padding-left: 1rem;
  }
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */

/* Internet Explorer */

/* Opera < 12.1 */
.menu {
  display: flex;
  flex-direction: column;
  position: fixed; top: 0; right: 0;
  padding: 1rem;
  margin: 1rem;
  z-index: 2;
  /* background: #232323;
  border-radius: 40px; */
}

.menu .icon {
  width: 30px;
  height: 30px;
  opacity: 0.5;
}

.menu a {
  text-align: center;
}

.menu a:not(:last-child) {
  margin-bottom: 1.25rem;
}

.menu .icon:hover {
  opacity: 1;
}

.menu .active .icon {
  fill: #2be2ff;
  opacity: 1;
}

.link--disabled {
  pointer-events: none;
  opacity: 0.3;
}

@media screen and (max-width: 640px) {
  .menu {
    flex-direction: row;
    justify-content: space-evenly;
    position: fixed;
    top: auto;
    top: initial;
    bottom: 0;
    width: 100%;
    padding: 0;
    margin: 0;
  }
}
.cursor_placeholder {
  display: flex;
  width: 150px;
  top: -30px;
  left: 5px;
  position: relative;
  font-size: 0.75rem;
}

.cursor_placeholder div {
  position: relative;
  top: -5px;
  left: 5px;
}
#main_about:focus {
  outline: none;
}

.about {
  display: flex;
  flex-direction: column;
}

.about-header {
  font-size: 2rem;
  height: 2rem;
}

.about-content {
  width: 50%;
  margin: 2rem 5rem 2rem 3rem
}

.about-content.hide {
  visibility: hidden;
}

.about-info {
  position: absolute;
  top: 0;
  font-size: 0.75rem;
}

@media screen and (max-width: 640px) {
  .about-content {
    width: auto;
  }
}

@media screen and (max-width: 400px) {
  .about-content {
    margin-right: 1rem;
    margin-left: 1.25rem;
  }
}
.intro {
  display: flex;
  cursor: default;
}

.intro .container {
  display: flex;
  letter-spacing: 0.1rem;
}

.intro-greeting {
  font-weight: 700;
  font-size: 5rem;
  padding-bottom: 1rem;
}

.intro-greeting div {
  height: 5rem !important;
}

.intro-introduce {
  font-weight: 700;
  font-size: 2rem;
  padding-bottom: 1.25rem;
}

.intro-introduce div {
  height: 2rem !important;
}

.intro-role {
  letter-spacing: 0rem !important;
  opacity: 0.9;
  padding-bottom: 1.5rem;
}

.intro-role div {
  height: 1rem !important;
}

.icon {
  fill: #fff;
  opacity: 0.8;
  width: 20px;
  height: 20px;
}

.icon:hover {
  fill: #2be2ff;
  opacity: 1;
}

.intro-socials {
  display: flex;
}

.intro-socials div {
  width: 20px !important;
  height: 20px !important;
  padding-right: 1rem;
}

@media screen and (max-width: 760px) {
  .intro-greeting {
    font-size: 4rem;
    padding-bottom: 0rem;
  }

  .intro-greeting div {
    height: 4rem;
  }

  .intro-introduce {
    font-size: 1.5rem;
    letter-spacing: 0.05rem !important;
    padding-bottom: 0.75rem;
  }

  .intro-introduce div {
    height: 1.5rem;
  }

  .intro-role {
    font-size: 0.9rem;
    padding-bottom: 1rem;
  }

  .intro-role div {
    height: 0.9rem;
  }

  .icon {
    width: 16px;
    height: 16px;
  }

  .intro-socials div {
    width: 16px !important;
    height: 16px !important;
    padding-right: 0.75rem;
  }
}

@media screen and (max-width: 640px) {
  .intro-greeting {
    font-size: 3rem;
    padding-bottom: 0.25rem;
  }

  .intro-greeting div {
    height: 3rem !important;
  }

  .intro-introduce {
    font-size: 1.25rem;
    letter-spacing: 0.05rem !important;
    padding-bottom: 0.5rem;
  }

  .intro-introduce div {
    height: 1.25rem !important;
  }

  .intro-role {
    font-size: 0.8rem;
  }

  .intro-role div {
    height: 0.8rem !important;
  }

  .icon {
    width: 15px;
    height: 15px;
  }

  .intro-socials div {
    width: 15px !important;
    height: 15px !important;
    padding-right: 0.5rem;
  }
}

@media screen and (max-width: 400px) {
  .intro-greeting {
    font-size: 2.75rem;
  }

  .intro-greeting div {
    height: 2.75rem !important;
  }

  .intro-introduce {
    font-size: 1rem;
  }
  
  .intro-role {
    font-size: 0.75rem;
    /* padding: 0.25rem 0 0.5rem; */
  }

  .intro-role div {
    height: 0.75rem !important;
  }

  .icon {
    width: 15px;
    height: 15px;
  }

  .intro-socials div {
    width: 15px !important;
    height: 15px !important;
    padding-right: 0.5rem;
  }
}


.canvas-sphere canvas:focus {
  outline: none;
}

.canvas-sphere {
  position: absolute !important;
}
.home {
  height: 100%;
  width: 100%;
  position: relative;
  display: flex;
}

.home__spheres {
  position: fixed !important;
  width: 100% !important;
  height: 100%;
  z-index: -1; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
  animation: fadein 5s;
}

